import fetchData from "./useFetch";
import { DataGrid } from '@mui/x-data-grid';
import { useEffect, useState } from "react";
import NumericChart from "./numeric-chart";

const columns = [
    { field: 'timestamp', headerName: 'Timestamp', width: 450 },
    { field: 'value', headerName: 'Value', width: 350 },
];

const host = "http://127.0.0.1:8000"

const Home = () => {
    const { data: filters } = fetchData(`${host}/filter-data`)
    const [dataType, setDataType] = useState("")
    const [selectedSensor, setSelectedSensor] = useState("")
    const [sensors, setSensors] = useState([])
    const [sensorData, setSensorData] = useState([])

    const changeDataType = (dataType) => {
        const sensors = filters[dataType]
        setSensors(sensors)
        setSelectedSensor(sensors[0])
        setDataType(dataType)
    }

    useEffect(() => {
        if(!selectedSensor){
            return
        }

        fetch(`${host}/sensors/${selectedSensor}`)
            .then(res => {
                if (!res.ok) {
                    throw Error("Could not fetch data")
                }
                return res.json()
            })
            .then(data => {
                console.log("changed",data)
                setSensorData(data)
            })
            .catch(error => {
                console.error(error)
            })

    }, [selectedSensor])

    const firstLetterUppercase = (word) => {
        return word.charAt(0).toUpperCase() + word.slice(1)
    }

    useEffect(() => {
        if (filters) {
            const key = Object.keys(filters)[0]
            changeDataType(key)
        }
    }, [filters])

    return (
        <div className="home">
            <h2>Home</h2>
            <div className="form-row">
                <form>
                    <select
                        value={dataType}
                        onChange={(e) => changeDataType(e.target.value)}
                    >   
                        {filters && Object.keys(filters).map(filter => <option value={filter}>{firstLetterUppercase(filter)}</option>)}
                    </select>

                    <select
                        value={selectedSensor}
                        onChange={(e) => setSelectedSensor(e.target.value)}
                    >
                        {sensors && sensors.map(sensor => <option value={sensor}>{firstLetterUppercase(sensor)}</option>)}

                    </select>
                </form>
            </div>
            {dataType === "text" && <DataGrid rows={sensorData} columns={columns} />}         
            {dataType === "numeric" &&  sensorData.length > 0 && <NumericChart data={sensorData}/>}
        </div>
    );
}

export default Home;